\n\tmargin:0;\r\n\ttext-align:center;\r\n\tpadding:30px 0 30px 0;\r\n}\r\n\r\n\r\n/* ===============================\r\n14. Blog & article\r\n================================ */\r\n\r\narticle{\r\n\tmargin-bottom:50px;\r\n\tborder-bottom: 1px solid #eee;\r\n\tpadding-bottom: 30px;\r\n}\r\n\r\narticle .post-heading h3 {\r\n\tmargin-bottom:20px;\r\n}\r\n\r\narticle .post-heading h3 a {\r\n\tfont-weight:700;\r\n\tcolor:#353535;\r\n}\r\n\r\narticle .post-heading h3 a:hover {\r\n\ttext-decoration:none;\r\n}\r\n\r\n.searchtitle {\r\n\tborder-bottom: 1px dotted #eee;\r\n}\r\n\r\n\r\n/* --- post meta --- */\r\n.post-meta {\r\n\tbackground:#fcfcfc;\r\n\tborder-top:none;\r\n\ttext-align:center;\r\n}\r\n\r\n.post-meta .format {\r\n\tpadding:10px 0 10px;\r\n}\r\n\r\n.post-meta i{\r\n\tmargin:0;\r\n}\r\n\r\n.post-meta .date {\r\n\tpadding:10px 0 10px;\r\n}\r\n\r\n.post-meta .date span {\r\n\ttext-align:center;\r\n\tcolor:#999;\r\n\tfont-size:12px;\r\n\tfont-weight:600;\r\n}\r\n\r\n.post-meta .comments {\r\n\tpadding:10px 0 10px;\r\n}\r\n\r\n.post-meta .comments a {\r\n\tcolor:#999;\r\n\tfont-size:12px;\r\n\tfont-weight:700;\r\n}\r\n\r\n.post-meta .comments a:hover {\r\n\ttext-decoration:none;\r\n}\r\n\r\na.readmore {\r\n\tfont-weight: 600;\r\n\tfont-size: 13px;\r\n}\r\n\r\n/* --- post format --- */\r\n\r\n.post-image {\r\n\tmargin-bottom:20px;\r\n}\r\n\r\n.post-quote {\r\n\tmargin-bottom:20px;\r\n}\r\n\r\n.post-video {\r\n\tmargin-bottom:20px;\r\n}\r\n\r\n.post-audio {\r\n\tmargin-bottom:20px;\r\n}\r\n\r\n.post-link {\r\n\tmargin-bottom:20px;\r\n}\r\n\r\n.post-slider {\r\n\tmargin-bottom:20px;\r\n}\r\n\r\n/* post-slider */\r\n\r\n.postslider {\r\n\tmargin-bottom: 50px;\r\n}\r\n\r\n.postslider.flexslider { margin: 0 0 60px; background: #fff; border: none;border-radius: 0; \r\n-o-box-shadow: none;\r\nbox-shadow: none;\r\n}\r\n.postslider  .flex-direction-nav a:before  { \r\n\tfont-family: \"flexslider-icon\"; \r\n\tfont-size: 20px; \r\n\tdisplay: inline-block; \r\n\tcontent: '\\F0