\F001'; \r\n}\r\n.postslider  .flex-direction-nav a.flex-next:before  { content: '\\F002'; }\r\n\r\n/* quote post */\r\n.post-quote blockquote {\r\n\tfont-size:16px;\r\n\tcolor:#999;\r\n\tfont-family:'Roboto', sans-serif;\r\n\tfont-style:italic;\r\n\tline-height:1.6em;\r\n}\r\n\r\n.post-quote blockquote i {\r\n\tfont-size: 40px;\r\n\tmargin:0 20px 10px 0;\r\n\tfloat: left;\r\n\tcolor: #eee;\r\n}\r\n\r\n.post-image img:hover {\r\n  box-shadow: 0 0 2px #979797;\r\n  transition: all 1s ease-in-out;\r\n}\r\n\r\nul.meta-post{\r\n\tmargin: 0;\r\n\tpadding:0;\r\n\tlist-style:none;\r\n}\r\n\r\nul.meta-post li{\r\n\tdisplay: inline-block;\r\n\tmargin:0 10px 0 10px;\r\n\tpadding:0;\r\n\tlist-style:none;\r\n\tpadding-right:10px;\r\n}\r\n\r\nul.meta-post li i {\r\n\tfloat:left;\r\n\tfont-size:12px;\r\n\tmargin:6px 5px 0 0;\r\n}\r\n\r\nul.meta-post li a{\r\n\tcolor:#999;\r\n\tfont-size:12px;\r\n\tfont-weight:600;\r\n}\r\n\r\n/* --- single post --- */\r\n.bottom-article{\r\n\toverflow: hidden;\r\n\tpadding:10px 0 10px 0;\r\n\tmargin-top:20px;\r\n}\r\n\r\n/* --- author info --- */\r\n\r\n.about-author {\r\n\tborder:1px solid #f5f5f5;\r\n\tpadding:20px;\r\n\tfloat:left;\r\n\tmargin-bottom:5px;\r\n}\r\n\r\n.about-author h5 {\r\n\tmargin-bottom:10px;\r\n}\r\n\r\n\r\n\r\n/* --- Comment --- */\r\n\r\n.comment-area {\r\n\tfloat:left;\r\n\twidth:100%;\r\n\tmargin-top:30px;\t\r\n}\r\n\r\n.comment-area:after {\r\n\t  display: table;\r\n  content: \" \";\r\n\r\n}\r\n\r\n.media-content{\r\n\toverflow:hidden;\r\n\tmargin-left:12px;\r\n\tborder-bottom:1px dotted #d5d5d5;\r\n\tpadding-bottom:20px;\r\n}\r\n\r\n.media-content h6 {\r\n\tfont-size:14px;\r\n\tfont-weight:700;\r\n}\r\n\r\n.media-content span{\r\n\tcolor:#a9a9a9;\r\n\tfont-size:14px;\r\n\tfont-weight:300;\r\n}\r\n\r\n.comment-area .thumbnail > img {\r\n  display: block;\r\n\twidth:60px;\r\n\theight:60px;\r\n}\r\n.filter-list{\r\n\tmargin-top:20px;\r\n}\r\n\r\n.comment-area .media a.reply {\r\n\tfont-weight: 600;\r\n}\r\n\r\n/* --- pagination --- */\r\n\r\n#pagination a,\r\n#pagination span {\r\n    display: block;\r\n    float: left;\r\n    margin: 0 7px 0 0;\r\n    padding: 7px 10px 6px 10px;\r\n    font-size: 12px;\r\n    line-height:12px;\r\n    color: #888;\r\n\tfont-weight:600;\r\nborder-radius:  2px;\r\n}\r\n\r\n#pagination a:hover {\r\n    color: #fff;\r\n\ttext-decoration:none;\r\n}\r\n\r\n#pagination span.current {\r\n    background: #333;\r\n    color: #fff;\r\n    font-weight: bold;\r\n}\r\n\r\n\r\n/* ===============================\r\n15. Aside, sidebar\r\n================================ */\r\n\r\naside{\r\n\tposition:relative;\r\n\tmargin-bottom:40px;\r\n}\r\n\r\n.sidebar-nav{\r\n\tfloat:left;\r\n\twidth:100%;\r\n}\r\n\r\n.right-sidebar{\r\n\tborder-left:1px solid #f5f5f5;\r\n\tpadding:0 0 0 30px;\r\n}\r\n\r\n\r\n.left-sidebar{\r\n\tborder-right:1px solid #f5f5f5;\r\n\tpadding:0 30px 0 0;\r\n}\r\n\r\n\r\n.left-sidebar .widget h3, .left-sidebar .widget .widget-content{\r\n\tpadding-right:20px;\r\n}\r\n\r\n\r\n\r\naside .widget {\r\n\tmargin-bottom:40px;\r\n}\r\n\r\n\r\nul.cat, ul.recent, ul.folio-detail, ul.contact-info{\r\n\tmargin:0;\r\n\tpadding:0;\r\n\tlist-style:none;\r\n}\r\n\r\nul.cat li, ul.recent li, ul.folio-detail li, ul.contact-info li{\r\n\tmargin:0 0 15px 0;\r\n\tlist-style:none;\r\n}\r\n\r\nul.cat li a, ul.folio-detail li a{\r\n\tcolor:#656565;\r\n}\r\n\r\nul.cat li i {\r\n\tmargin-right: 10px;\r\n}\r\n\r\nul.folio-detail li label{\r\n\tdisplay:inline;\r\n\tcursor:default;\r\n\tcolor:#353535;\r\n\tfont-weight:bold;\r\n}\r\n\r\n.widget ul.recent li  {\r\n\tdisplay:inline-block;\r\n}\r\n\r\n.widget ul.recent li {\r\n\tmargin-bottom:20px;\r\n}\r\n\r\nul.recent li h6 {\r\n\tmargin:0 0 10px 0;\r\n}\r\n\r\nul.recent li h6 a{\r\n\tcolor:#353535;\r\n\tfont-size:16px;\r\n\tfont-weight:600;\r\n}\r\n\r\n\r\n.widget ul.tags {\r\n\tlist-style:none;\r\n\tmargin:0;\r\n\tmargin-left: 0;\r\n\tpadding-left:0;\r\n}\r\n\r\n.widget ul.tags li {\r\n\tmargin:0 5px 15px 0;\r\n\tdisplay:inlin